.button {
    min-width: 3rem;
    margin: 1rem;
    padding: 0.5rem;
    background-color: #d7e4f8;
    border-radius: 10px;
    border: 2px solid #85a3da;
    color: black;
    transition-duration: 0.3s;
  }
  .button:hover {
    background-color: white;
    color: black;
    border-width: 2px;
  }